import React, { useState } from 'react';
import {
  List,
  Picker,
  WhiteSpace,
  TextareaItem,
  ImagePicker,
  Toast,
} from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { useMutation } from '@apollo/client';
import PickItem from '@/components/PickItem';
import { feedbackType } from '@/constants/globalDatas';
import { CREAT_FEEDBACK } from '@/helpers/APIHelper';
import { Page, PageTitle, PrimaryButton } from '../../Login/styles';
import { ImageFile } from '../types';

const Feedback: React.FC = () => {
  const [feedType, setFeedType] = useState<string[]>([]); // 反馈类型
  const [feedContent, setFeedContent] = useState(''); // 反馈内容
  const [fileArr, setFileArr] = useState<ImageFile[]>([]); // 反馈图片
  const [handleSave, { loading }] = useMutation(CREAT_FEEDBACK, {
    onCompleted: () => changeSuccess(),
  });
  const history = useHistory();
  // 选择图片
  const onChange = (files: ImageFile[], type: string, index?: number): void => {
    setFileArr(files);
    console.log(files, type, index);
  };

  // 页面校验
  const checkValues = (): boolean => {
    switch (false) {
      case feedType.length > 0:
        Toast.info('请选择反馈类型');
        break;
      case !!feedContent:
        Toast.info('请输入反馈内容');
        break;
      default:
        return true;
    }
    return false;
  };

  // 提交
  const handleSubmit = (): void => {
    console.log(fileArr);
    if (checkValues()) {
      handleSave({
        variables: {
          id: '111',
          fbType: feedType[0],
          fbContent: feedContent,
          fbImage: fileArr[0].url,
        },
      });
    }
    // history.goBack();
  };

  // 保存成功
  const changeSuccess = (): void => {
    Toast.info('保存成功', 1.5, () => history.goBack());
  };
  return (
    <Page>
      <PageTitle>意见反馈</PageTitle>
      <WhiteSpace size="xl" />

      <Picker
        data={feedbackType}
        cols={1}
        value={feedType}
        onOk={(value: string[]): void => setFeedType(value)}
      >
        <PickItem placeholder="请选择反馈问题类型" />
      </Picker>
      <List>
        <TextareaItem
          placeholder="请输入您的意见"
          rows={5}
          count={100}
          clear
          onChange={(val) => setFeedContent(val || '')}
        />
      </List>
      <WhiteSpace size="xl" />
      <ImagePicker
        onChange={onChange}
        files={fileArr}
        selectable={fileArr.length < 4}
      />
      <WhiteSpace size="xl" />

      <PrimaryButton type="primary" mt="200px" onClick={handleSubmit}>
        提交
      </PrimaryButton>
    </Page>
  );
};

export default Feedback;
